input[type="text"],
div[contenteditable="true"],
textarea {
  font-size: 16px;
  border: 1px solid darken($colour-base, 10%);
  border-radius: 4px;
  padding-top: 5px;
  padding-right: 9px;
  padding-left: 9px;
  padding-bottom: 5px;

  &.round {
    border-radius: 16px;
  }

  &:focus {
    outline: 0;
  }
}
